body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

li {
  list-style-type: none;
}

.App {
  height: 95vh;
}

.header {
  display: flex;
  background-color: black;
  color: white;
  height: 5vh;
}

.header h1 {
  margin: 0px;
  margin-left: auto;
  margin-right: auto;
}

.app-wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}

#list-wrapper {
  display: block;
  width: 100%;
  /*height: 100%;*/
  margin-top: 10px;
  overflow: auto;
}


#map {
  width: 100%;
  height: 75%;
}

.location-name {
  height: 100px;
}


.location-info {
  border: 2px solid;
  width: 95%;
  min-height: 100px;
  margin: 10px auto;
  max-width: 50%;
}

.location-address {
  border: 1px solid;
}

.hamburger {
  width: 48px;
  height: 48px;
  background-image: url(/fend-neighborhood-map/static/media/if_menu-alt_134216.e8e2b391.svg);
  background-repeat: no-repeat;
  background-position: center;
}

@media screen and (min-width: 768px) {
  #list-wrapper {
    order: 0;
    height: 100%;
    width: 25%;
  }
  #map {
    order: 1;
    width: 75%;
    height: 100%;
  }
}

#streetview {
  width: 200px;
  height: 200px;
}
